import useTransaction from '@/hooks/common/useTransaction'
import useGlobalStore from '@/store/common/global'
import useUnstableVaultsListStore from '@/store/unstable-vaults/useVaultsList'
import { MsafeTransactionSubType } from '@/types'
import { AssetActionType } from '@/types/vaults'
import { formatDescription } from '@/utils'
import { useDebounceFunction } from '@cetus/hooks'
import { useGetTokenBalance } from '@cetus/hooks/src/useTokenBalance'
import useTokenPrice from '@cetus/hooks/src/useTokenPrice'
import usePeripherySDKStore from '@cetus/stores/src/usePeripherySDKStore'
import { BalanceChanges, CommonTypeInfo, ToastType, Token, TransactionStatusType } from '@cetus/types'
import { convertScientificToDecimal, d, formatNumber, fromDecimalsAmountFix } from '@cetus/utils'
import { CoinAssist, fromDecimalsAmount, toDecimalsAmount } from '@cetusprotocol/cetus-sui-clmm-sdk'
import { CalculateAmountResult } from '@cetusprotocol/vaults-sdk'
import { useEffect, useMemo, useRef, useState } from 'react'
import { v4 } from 'uuid'
import { DepositCalculationOptions } from 'volatile-vault-sdk'
import { useGetSuiStakeProtocol } from './useVaultsHelper'

export default function useUnstableVaultsAdd(
  assetAction: AssetActionType,
  autoRefresh: () => void,
  vaultsId?: string,
  tokenA?: Token,
  tokenB?: Token,
  isReverse?: boolean,
  isCheckedZAP?: boolean,
  hardCapUSD?: string,
  vaultTvl?: string
) {
  const { vaultsSlippage, mevProtect, maxCapForGas, transactionMode, customGasPrice } = useGlobalStore()

  const { fetchTokenPrices, getTokenAmountValue } = useTokenPrice()
  const { setDetailRefreshCount } = useUnstableVaultsListStore()
  const { volatileVaultsSdk } = usePeripherySDKStore()

  const [amountInputA, setAmountInputA] = useState<string>('')
  const [amountInputB, setAmountInputB] = useState<string>('')
  const [fixAmountA, setFixAmountA] = useState<boolean>(true)
  const [uuid, setUuid] = useState<string>('')

  const uuidRef = useRef<string>('')
  const fixAmountARef = useRef<boolean>(true)
  const amountInputARef = useRef<string>('')
  const amountInputBRef = useRef<string>('')

  useEffect(() => {
    uuidRef.current = uuid
  }, [uuid])

  useEffect(() => {
    fixAmountARef.current = fixAmountA
  }, [fixAmountA])

  useEffect(() => {
    amountInputARef.current = isReverse ? amountInputA : amountInputA
  }, [amountInputA, isReverse, amountInputB])

  useEffect(() => {
    amountInputBRef.current = isReverse ? amountInputB : amountInputB
  }, [amountInputB, isReverse, amountInputA])

  useEffect(() => {
    resetInputAmount()
  }, [isCheckedZAP])

  //Ëé∑Âèñ‰ª∑Ê†º
  useEffect(() => {
    const coinTypeList: string[] = []
    if (tokenA && tokenB) {
      coinTypeList.push(tokenA.coin_type)
      coinTypeList.push(tokenB.coin_type)
    }
    if (coinTypeList.length > 0) {
      fetchTokenPrices(coinTypeList)
    }
  }, [tokenA?.coin_type, tokenB?.coin_type])

  // ‰ΩôÈ¢ù Âõ†‰∏∫È°µÈù¢‰∏ä‰º†ÂÖ•Êó∂ÊåâÁÖßÂêàÁ∫¶ÊñπÂêë‰º†ÂÖ• ÊâÄ‰ª•ËøôÈáåÈúÄË¶ÅÂèçËøáÊù•
  const { balanceInfo: balanceInfoA } = useGetTokenBalance(isReverse ? tokenB : tokenA)
  const { balanceInfo: balanceInfoB } = useGetTokenBalance(isReverse ? tokenA : tokenB)

  // ‰ª∑ÂÄº Âõ†‰∏∫È°µÈù¢‰∏ä‰º†ÂÖ•Êó∂ÊåâÁÖßÂêàÁ∫¶ÊñπÂêë‰º†ÂÖ• ÊâÄ‰ª•ËøôÈáåÈúÄË¶ÅÂèçËøáÊù•
  const amountValueA = getTokenAmountValue(isReverse ? tokenB?.coin_type : tokenA?.coin_type, amountInputA)
  const amountValueB = getTokenAmountValue(isReverse ? tokenA?.coin_type : tokenB?.coin_type, amountInputB)
  // ÊÄª‰ª∑ÂÄº
  const inputTotalValue = useMemo(() => {
    return d(amountValueA || '0')
      .add(amountValueB || '0')
      .toString()
  }, [amountValueA, amountValueB])

  const zapNumError = useMemo(() => {
    if (isCheckedZAP) return d(inputTotalValue).gt(d(10000))
  }, [inputTotalValue, isCheckedZAP])

  const overHardtop = useMemo(() => {
    if (hardCapUSD && inputTotalValue && d(hardCapUSD).gt(0)) {
      if (d(inputTotalValue).add(d(vaultTvl)).gt(hardCapUSD)) {
        return true
      }
    }
    return false
  }, [hardCapUSD, inputTotalValue, vaultTvl])

  // È¢ÑËÆ°ÁÆóÁªìÊûú
  const [preCalculateLoading, setPreCalculateLoading] = useState<boolean>(false)
  const [calculateLpLoading, setCalculateLpLoading] = useState<boolean>(false)
  const [calculateResult, setCalculateResult] = useState<CalculateAmountResult | undefined>(undefined)
  // ÊòØÂê¶Ë∂ÖËøáÁ°¨È°∂
  // const [overHardtop, setOverHardtop] = useState(false)
  // È¢ÑËÆ°ÁÆó
  const preCalculate = async (amount: string, fix_amount_a: boolean, uuid: string, isReverse = false, other_amount: string) => {
    console.log('üöÄüöÄüöÄ ~ useVaultsAdd.ts:102 ~ preCalculate ~ uuid:', uuid)
    console.log('üöÄüöÄüöÄ ~ useVaultsAdd.ts:102 ~ preCalculate ~ amount:', amount, other_amount)
    if (tokenA && tokenB && vaultsId) {
      try {
        // setOverHardtop(false)
        // setPreCalculateLoading(true)
        setCalculateLpLoading(true)
        setDetailRefreshCount(0)
        const fixToken = fix_amount_a ? tokenA : tokenB
        const fixOtherToken = fix_amount_a ? tokenB : tokenA
        const inputAmount = toDecimalsAmount(amount, fixToken.decimals).toFixed(0)
        const otherAmount = toDecimalsAmount(other_amount || 0, fixOtherToken.decimals).toFixed(0)
        // ÂºÄÂêØzapÊ®°Âºè
        let mode
        if (isCheckedZAP) {
          console.log('üöÄüöÄüöÄ ~ useVaultsAdd.ts:107 ~ preCalculate ~ assetAction:', assetAction)
          mode = assetAction == 'both' ? 'FlexibleBoth' : assetAction == tokenA?.coin_type ? 'OnlyCoinA' : 'OnlyCoinB'
        } else {
          mode = 'FixedOneSide'
        }
        let params: DepositCalculationOptions = {
          uuid,
          mode: 'FixedOneSide',
          fixed_amount: '0',
          fixed_coin_a: true,
          pool_id: vaultsId
        }

        if (isCheckedZAP) {
          if (mode === 'OnlyCoinB') {
            params = {
              uuid,
              mode: 'OnlyCoinB',
              pool_id: vaultsId,
              coin_amount_b: inputAmount
            }
          }
          if (mode === 'OnlyCoinA') {
            params = {
              uuid,
              mode: 'OnlyCoinA',
              pool_id: vaultsId,
              coin_amount_a: inputAmount
            }
          }
          if (mode == 'FlexibleBoth') {
            params = {
              uuid,
              mode: 'FlexibleBoth',
              pool_id: vaultsId,
              coin_amount_a: fix_amount_a ? inputAmount : otherAmount,
              coin_amount_b: fix_amount_a ? otherAmount : inputAmount
            }
          }
        } else {
          params = {
            uuid,
            mode: 'FixedOneSide',
            fixed_amount: inputAmount,
            fixed_coin_a: fix_amount_a,
            pool_id: vaultsId
          }
        }

        const res = await volatileVaultsSdk.Vaults.preCalculateDepositAmount(params, {
          onSuccess: (receive_lp_amount, uuid) => {
            console.log('üöÄüöÄüöÄ ~ useVaultsAdd.ts:171 ~ onSuccess ~ receive_lp_amount, uuid:', receive_lp_amount)
            if (uuid == params.uuid) {
              setCalculateResult({
                amount_a: isReverse ? res.deposit_amount_b : res.deposit_amount_a,
                amount_b: isReverse ? res.deposit_amount_a : res.deposit_amount_b,
                ft_amount: receive_lp_amount
              })
              setCalculateLpLoading(false)
            }
          },
          onError: (error, uuid) => {
            console.log('üöÄ ~ preCalculate ~ error:', error)
          }
        })
        console.log('üöÄ ~ preCalculate ~ res:', {
          ...res,
          uuid,
          assetAction,
          '+amountInputARef.current': +amountInputARef.current
        })
        // È¢ÑËÆ°ÁÆó‰πãÂêésdkÊåâÁÖßÂêàÁ∫¶ÊñπÂêëËøîÂõû ËøôÈáåÂ§ÑÁêÜÊñπÂêë
        const wrapRes = {
          amount_a: isReverse ? res.deposit_amount_b : res.deposit_amount_a,
          amount_b: isReverse ? res.deposit_amount_a : res.deposit_amount_b,
          ft_amount: res.receive_lp_amount
        }
        console.log('üöÄüöÄüöÄ ~ useVaultsAdd.ts:204 ~ preCalculate ~ wrapRes:', wrapRes)
        if (wrapRes.ft_amount == '0') {
          // setPreCalculateLoading(false)
          setCalculateResult(undefined)
          return
        }

        if (uuidRef.current !== uuid) return // Áõ¥Êé•ËøîÂõûÔºåÂáèÂ∞ëÂµåÂ•óÂ±ÇÁ∫ß

        console.log('üöÄüöÄüöÄ ~ useVaultsAdd.ts:162 ~ preCalculate ~ assetAction:', assetAction)

        if (isCheckedZAP) {
          setCalculateResult(wrapRes)
          return
        }

        // Â§ÑÁêÜÈùû ZAP Ê®°Âºè
        if (assetAction === 'both') {
          // fix_amount_a ÊñπÂêëËΩ¨Êç¢ÔºàÂêàÁ∫¶ÊñπÂêë -> È°µÈù¢ÊñπÂêëÔºâ
          const fix_amount_a_page = isReverse ? !fix_amount_a : fix_amount_a
          console.log('üöÄüöÄüöÄ ~ useVaultsAdd.ts:164 ~ preCalculate ~ fix_amount_a_page:', fix_amount_a_page)
          console.log('üöÄüöÄüöÄ ~ useVaultsAdd.ts:168 ~ preCalculate ~ +amountInputARef.current:', +amountInputARef.current)

          if (fix_amount_a_page) {
            if (+amountInputARef.current) {
              console.log('üöÄüöÄüöÄ ~ useVaultsAdd.ts:173 ~ preCalculate ~ setAmountInputB:', setAmountInputB)
              setAmountInputB(fromDecimalsAmountFix(wrapRes.amount_b.toString(), isReverse ? tokenA?.decimals : tokenB?.decimals).toString())
            } else {
              setAmountInputB('')
            }
          } else {
            if (+amountInputBRef.current) {
              setAmountInputA(fromDecimalsAmountFix(wrapRes.amount_a.toString(), isReverse ? tokenB?.decimals : tokenA?.decimals).toString())
            } else {
              setAmountInputA('')
            }
          }
        }

        // ÂçïËæπËµÑ‰∫ßËÆ°ÁÆó
        if (assetAction !== 'both' || !isCheckedZAP) {
          setCalculateResult(wrapRes)
        }
      } catch (error) {
        console.log('üöÄüöÄüöÄ ~ useVaultsAdd.ts:202 ~ preCalculate ~ error:', error)
        // Ë∂ÖËøáÁ°¨È°∂
        if (String(error).indexOf('has reached hardCap') > -1) {
          // setOverHardtop(true)
          // setPreCalculateLoading(false)
        }
        console.log('üöÄ ~ preCalculate ~ error:', error)
        setCalculateResult(undefined)
      } finally {
        // setPreCalculateLoading(false)
      }
    }
  }
  const debouncedPreCalculate = useDebounceFunction(preCalculate, 500)

  // ËæìÂÖ•Êï∞ÈáèÁõëÂê¨ // other_amount ÂºÄÂêØzap ÂèØ‰ª•Ëá™Áî±ËæìÂÖ•A„ÄÅBÊï∞Èáè
  const handleAmountInputChange = (amount: string, fix_amount_a: boolean, resetTargetInput: boolean = true, other_amount: string) => {
    console.log('üöÄüöÄüöÄ ~ useVaultsAdd.ts:210 ~ handleAmountInputChange ~ amount:', amount, other_amount)
    setFixAmountA(fix_amount_a)
    if (!amount && !other_amount) {
      setCalculateResult(undefined)
      // setPreCalculateLoading(false)
      setAmountInputA('')
      setAmountInputB('')
      return
    }
    if (isReverse ? !fix_amount_a : fix_amount_a) {
      setAmountInputA(amount)
      if (resetTargetInput && !isCheckedZAP) {
        setAmountInputB('')
      }
    } else {
      setAmountInputB(amount)
      if (resetTargetInput && !isCheckedZAP) {
        setAmountInputA('')
      }
    }

    if (+amount) {
      if (isCheckedZAP && assetAction == 'both' && (!amount || !other_amount)) {
        setCalculateResult(undefined)
        // setPreCalculateLoading(false)
        return
      }
      const uuid = v4()
      console.log('üöÄüöÄüöÄ ~ useVaultsAdd.ts:300 ~ handleAmountInputChange ~ amount:', amount)
      console.log('üöÄüöÄüöÄ ~ useVaultsAdd.ts:300 ~ handleAmountInputChange ~ other_amount:', other_amount)
      console.log('üöÄüöÄüöÄ ~ useVaultsAdd.ts:255 ~ handleAmountInputChange ~ uuid:', uuid)
      setUuid(uuid)
      if (assetAction === 'both') {
        // setPreCalculateLoading(true)
      }
      debouncedPreCalculate(amount, fix_amount_a, uuid, isReverse, other_amount)
    } else {
      if (!isCheckedZAP) {
        resetInputAmount()
      } else if (assetAction == 'both') {
        setCalculateResult(undefined)
      } else {
        if (assetAction == tokenA?.coin_type || assetAction == tokenB?.coin_type) {
          resetInputAmount()
        }
      }
    }
  }

  // ÈáçÊñ∞ËÆ°ÁÆó ‰ªçÈúÄËÄÉËôëÊñπÂêë
  const reCalculateResult = () => {
    const amount = fixAmountARef.current
      ? isReverse
        ? amountInputBRef.current
        : amountInputARef.current
      : isReverse
        ? amountInputARef.current
        : amountInputBRef.current
    const other_amount = fixAmountARef.current
      ? !isReverse
        ? amountInputBRef.current
        : amountInputARef.current
      : !isReverse
        ? amountInputARef.current
        : amountInputBRef.current
    if (isCheckedZAP && assetAction == 'both' && (!amount || !other_amount)) {
      return
    }
    if (+amount) {
      const uuid = v4()
      setUuid(uuid)
      debouncedPreCalculate(amount, fixAmountARef.current, uuid, isReverse, other_amount)
    }

    console.log('üöÄüöÄüöÄ ~ useVaultsAdd.ts:329 ~ reCalculateResult ~ other_amount:', other_amount)
    console.log('üöÄüöÄüöÄ ~ useVaultsAdd.ts:329 ~ reCalculateResult ~ amount:', amount)
  }

  // ÂàáÊç¢ËµÑ‰∫ßÁ±ªÂûãÔºåÊ∏ÖÁ©∫ËæìÂÖ•Ê°Ü
  useEffect(() => {
    resetInputAmount()
  }, [assetAction])

  const [submitLoading, setSubmitLoading] = useState<boolean>(false)
  const { signAndExecuteTransaction, transactionConfirmation, transactionRejected } = useTransaction()
  // Êèê‰∫§‰∫§Êòì
  const doAddAction = async () => {
    if (vaultsId) {
      setSubmitLoading(true)
      // Â§ÑÁêÜÈ°µÈù¢‰∏äÁöÑÂÄº ‰øùËØÅÊåâÁÖßÂêàÁ∫¶A,B‰º†ÂÖ•
      const depositParams = {
        pool_id: vaultsId,
        amount_a: String(
          isReverse ? toDecimalsAmount(amountInputB || 0, String(tokenA?.decimals)) : toDecimalsAmount(amountInputA || 0, String(tokenA?.decimals))
        ),
        amount_b: String(
          isReverse ? toDecimalsAmount(amountInputA || 0, String(tokenB?.decimals)) : toDecimalsAmount(amountInputB || 0, String(tokenB?.decimals))
        )
      }
      console.log('üöÄüöÄüöÄ ~ useVaultsAdd.ts:261 ~ doAddAction ~ depositParams:', depositParams)
      // ÊûÑÂª∫ ‰∫§ÊòìÊèêÁ§∫
      const toastInfo: ToastType = {
        getShowInfo: (status: TransactionStatusType, balanceChanges?: Record<string, BalanceChanges>, res?: any) => {
          console.log('üöÄüöÄüöÄ ~ useVaultsAdd.ts:306 ~ doAddAction ~ res:', res)
          // ÊèêÁ§∫ËØ≠‰πüÈúÄË¶ÅÂ§ÑÁêÜÊñπÂêë
          const description =
            'Deposit ' +
            [
              formatDescription(isReverse ? amountInputA : amountInputA, isReverse ? tokenB?.symbol : tokenA?.symbol),
              formatDescription(isReverse ? amountInputB : amountInputB, isReverse ? tokenA?.symbol : tokenB?.symbol)
            ]
              .filter(Boolean)
              .join(' and ')

          const info: CommonTypeInfo = {
            modalDescriptionText: description,
            toastTitleText: description
          }

          if (status === 'success') {
            let amountA = amountInputA
            let amountB = amountInputB

            if (res) {
              res.events.forEach(event => {
                console.log('üöÄüöÄüöÄ ~ useVaultsAdd.ts:367 ~ doAddAction ~ event:', event)
                if (event.type.indexOf('::pool::DepositEvent') > -1 && tokenA && tokenB) {
                  amountA = String(fromDecimalsAmount(event.parsedJson.amount_a, tokenA.decimals))
                  console.log('üöÄüöÄüöÄ ~ useVaultsAdd.ts:361 ~ doAddAction ~ amountA:', amountA)
                  amountB = String(fromDecimalsAmount(event.parsedJson.amount_b, tokenB.decimals))
                  console.log('üöÄüöÄüöÄ ~ useVaultsAdd.ts:363 ~ doAddAction ~ amountB:', amountB)
                }
              })
            }

            // if (balanceChanges) {
            //   amountA = d(amountInputA).gt(0) ? getBalanceChanges(balanceChanges, tokenA) || amountInputA : ''
            //   amountB = d(amountInputB).gt(0) ? getBalanceChanges(balanceChanges, tokenB) || amountInputB : ''
            // }
            const description =
              'Deposit ' +
              [
                formatDescription(isReverse ? amountB : amountA, isReverse ? tokenB?.symbol : tokenA?.symbol),
                formatDescription(isReverse ? amountA : amountB, isReverse ? tokenA?.symbol : tokenB?.symbol)
              ]
                .filter(Boolean)
                .join(' and ')

            info.toastDescriptionContent = description
            info.modalDescriptionText = description
            info.toastTitleText = 'Deposit successful'
          }

          return info
        }
      }
      transactionConfirmation(toastInfo)

      try {
        const txb = await volatileVaultsSdk.Vaults.buildDepositPayload(depositParams)
        const res = await signAndExecuteTransaction(txb, toastInfo, {
          useMev: mevProtect,
          txAction: 'signTransactionBlock',
          useFastMode: transactionMode === 'Fast Mode',
          maxCapForGas,
          customGasPrice,
          msafeParams: {
            action: MsafeTransactionSubType.AddVaultsPosition,
            txbParams: depositParams
          }
        })

        if (res) {
          resetInputAmount()
          autoRefresh()
        } else {
          reCalculateResult()
        }
      } catch (error) {
        transactionRejected(toastInfo)
      }
    }
  }

  // Ê∏ÖÁ©∫ËæìÂÖ•Ê°Ü
  const resetInputAmount = () => {
    setAmountInputA('')
    setAmountInputB('')
    // setPreCalculateLoading(false)
    setCalculateResult(undefined)
    setUuid('')
    // setOverHardtop(false)
  }
  const { stakeProtocolName } = useGetSuiStakeProtocol(!fixAmountARef.current ? tokenA?.coin_type : tokenB?.coin_type)
  // ÂçïËæπÊèêÁ§∫ÊñáÊ°à
  const showOnlySideTips = useMemo(() => {
    if (assetAction !== 'both' && tokenA && tokenB) {
      const amount = fixAmountARef.current ? amountInputA : amountInputB
      const fixToken = fixAmountARef.current ? tokenA : tokenB
      const targetToken = fixAmountARef.current ? tokenB : tokenA

      console.log('üöÄüöÄüöÄ ~ useVaultsAdd.ts:381 ~ showOnlySideTips ~ calculateResult:', calculateResult)
      if (calculateResult && calculateResult.swap_result) {
        const swapInAmount = fromDecimalsAmountFix(calculateResult.swap_result.swap_in_amount, fixToken.decimals)
        const swapOutAmount = fromDecimalsAmountFix(
          d(calculateResult.swap_result.swap_out_amount)
            .mul(1 - Number(vaultsSlippage))
            .toString(),
          targetToken.decimals
        )

        if (CoinAssist.isSuiCoin(fixToken.coin_type)) {
          return `By depositing ${convertScientificToDecimal(formatNumber(amount, 9).toString(), 9)} SUI, ${convertScientificToDecimal(formatNumber(swapInAmount, 9).toString(), 9)} SUI will be converted to ${targetToken?.symbol} via ${stakeProtocolName} liquid staking and added to the pool together.`
        }
        return `By depositing ${convertScientificToDecimal(formatNumber(amount, 9).toString(), 9)} ${fixToken?.symbol}, ${convertScientificToDecimal(formatNumber(swapInAmount, 9).toString(), 9)} ${fixToken?.symbol} will be traded for ${convertScientificToDecimal(formatNumber(swapOutAmount, 9).toString(), 9)} ${targetToken?.symbol} and then added to the pool together.`
      }
    }

    return undefined
  }, [assetAction, calculateResult, amountInputA, amountInputB])

  const inputTotalValueError = useMemo(() => {
    console.log('üöÄüöÄüöÄ ~ VaultsRemove.tsx:178 ~ inputTotalValueError ~ inputTotalValue:', inputTotalValue, isCheckedZAP)
    if (inputTotalValue && isCheckedZAP) {
      return d(inputTotalValue).gt(0) && d(inputTotalValue).lt(0.0001)
    }
    return false
  }, [isCheckedZAP, inputTotalValue])

  return {
    handleAmountInputChange,
    fixAmountA,
    amountInputA,
    amountInputB,
    balanceInfoA,
    balanceInfoB,
    amountValueA,
    amountValueB,
    preCalculateLoading,
    inputTotalValue,
    calculateResult,
    showOnlySideTips,
    doAddAction,
    reCalculateResult,
    overHardtop,
    zapNumError,
    calculateLpLoading,
    inputTotalValueError
  }
}
